import React from "react";
import Layout from "../components/layout"
import SEO from "../components/seo"
import github from "../images/GitHub-Mark-64px.png"
import react from "../images/react.png"
import node from "../images/node.png"
import js from "../images/js.png"
import c from "../images/c++.jpg"
import php from "../images/php.jpg"
import java from "../images/java.jpg"
import flutter from "../images/flutter.png"
import posed from 'react-pose'
import style from "../styles/projects.module.css"

const Projects = () => (
    <Layout>
        <SEO title="Projects" />
        <h2 >My Projects <a href="https://github.com/gakki88?tab=repositories"><HoverIcon src={github} style={{ display: 'inline-block', width: "30px", height: "30px", marginBottom: '0', marginLeft: '1.5rem' }} alt="github"></HoverIcon></a></h2>

        <br></br>

        <Project
            title="To-do list"
            icon={react}
            description="Basic single page web application using React for frontend and Node Express for backend."
            link="https://github.com/gakki88/todolist"
        />

        <Project
            title="Artificial Inteligent algorithm application"
            icon={js}
            description="Genetic Algorithm applied in class scheduling using JavaScript"
            link="https://github.com/gakki88/Genetic-Algorithm-applied-in-class-schedule"
        />

        <Project
            title="Static Website"
            icon={php}
            description="Simulated website to take bakery orders implemented in PHP"
            link="https://github.com/gakki88/online-bakery"
        />

        <Project
            title="Console Game"
            icon={c}
            description="Console game of withdrawing matches implemented in C++"
            link="https://github.com/gakki88/allumette"
        />

        <Project
            title="Mobile Game"
            icon={flutter}
            description="Story telling, interactive mobile game implemented in Flutter (Dart)"
            link="https://github.com/gakki88/City-Hero"
        />

        <ProjectNolink
            title="Stock Value Simulation"
            icon={java}
            description="Platform to analyse stock index values with basic technical method, implemented in Java EE"
        />

        <ProjectNolink
            title="Space Invader Game"
            icon={java}
            description="Game allowing multiple player to play identical map at the same time, implemented in Java"
        />
    </Layout>
)

const Project = (props) => (
    <HoverEffect className={style.box}>
        <h5>{props.title}<HoverIcon src={props.icon} style={{ display: 'inline-block', width: "30px", height: "30px", marginBottom: '0', marginLeft: '1.5rem' }}></HoverIcon></h5>
        <p>{props.description}</p>
        <a href={props.link}>View code in GitHub</a>
    </HoverEffect>
)

const ProjectNolink = (props) => (
    <HoverEffect className={style.box}>
        <h5>{props.title}<HoverIcon src={props.icon} style={{ display: 'inline-block', width: "30px", height: "30px", marginBottom: '0', marginLeft: '1.5rem' }}></HoverIcon></h5>
        <p>{props.description}</p>
    </HoverEffect>

)

const HoverEffect = posed.div({
    hoverable: true,
    init: {
        scale: 1,
        boxShadow: "0px 0px 0px rgba(0,0,0,0)"
    },
    hover: {
        scale: 1.1,
        boxShadow: "5px 5px 10px rgba(0,0,0,0.3)"
    }
})

const HoverIcon = posed.img({
    hoverable: true,
    init: {
        scale: 1,
        boxShadow: "0px 0px 0px rgba(0,0,0,0)"
    },
    hover: {
        scale: 1.1,
        boxShadow: "5px 5px 10px rgba(0,0,0,0.3)"
    }
})

export default Projects